.twentytwenty-container {
  box-sizing: content-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  border: 2px solid #000;
  margin: 5px auto;
  display: block;
  position: relative;
  overflow: hidden;
}

.twentytwenty-container img {
  max-width: auto;
  height: auto;
  display: block;
  position: absolute;
  top: 0;
}

.twentytwenty-container * {
  box-sizing: content-box;
}

.twentytwenty-frame-1, .twentytwenty-frame-2, .twentytwenty-frame-3, .twentytwenty-frame-4 {
  z-index: 30;
  display: block;
  position: absolute;
  box-shadow: 0 0 12px #00000080;
}

.twentytwenty-compare-2 .twentytwenty-frame-1 {
  cursor: col-resize;
  border-right: 1px solid #fff;
  top: 0;
  left: 0;
}

.twentytwenty-compare-2 .twentytwenty-frame-2 {
  cursor: col-resize;
  border-left: 1px solid #fff;
  top: 0;
  right: 0;
}

.twentytwenty-compare-3 .twentytwenty-frame-1, .twentytwenty-compare-4 .twentytwenty-frame-1 {
  cursor: move;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  top: 0;
  left: 0;
}

.twentytwenty-compare-3 .twentytwenty-frame-2, .twentytwenty-compare-4 .twentytwenty-frame-2 {
  cursor: move;
  border-bottom: 1px solid #fff;
  border-left: 1px solid #fff;
  top: 0;
  right: 0;
}

.twentytwenty-compare-3 .twentytwenty-frame-3 {
  cursor: move;
  border-top: 1px solid #fff;
  bottom: 0;
}

.twentytwenty-compare-4 .twentytwenty-frame-3 {
  cursor: move;
  border-top: 1px solid #fff;
  border-right: 1px solid #fff;
  bottom: 0;
  left: 0;
}

.twentytwenty-compare-4 .twentytwenty-frame-4 {
  cursor: move;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
  bottom: 0;
  right: 0;
}

.twentytwenty-overlay {
  z-index: 25;
  position: relative;
  overflow: hidden;
}

.twentytwenty-1 {
  z-index: 20;
}

.twentytwenty-2 {
  z-index: 10;
}

.twentytwenty-3 {
  z-index: 9;
}

.twentytwenty-4 {
  z-index: 8;
}

.twentytwenty-handle {
  z-index: 40;
  background: #000;
  border: 2px solid #fff;
  border-radius: 1000px;
  width: 40px;
  height: 40px;
  margin-top: -22px;
  margin-left: -22px;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 0 12px #00000080;
}

.twentytwenty-compare-2 .twentytwenty-handle {
  cursor: col-resize;
}

.twentytwenty-compare-3 .twentytwenty-handle, .twentytwenty-compare-4 .twentytwenty-handle {
  cursor: move;
}

.twentytwenty-label-1, .twentytwenty-label-2, .twentytwenty-label-3, .twentytwenty-label-4 {
  text-shadow: 0 0 1ex #000, 0 0 1ex #000;
